<template src="./template.html">

</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      loginForm: {
        username: '',
        password: '',
      },
      // image: require('@/assets/logo.png'),
      responseResult: []
    }
  },
  methods: {
    login() {
      this.$axios
          .post('/login', {
            username: this.loginForm.username,
            password: this.loginForm.password
          }).then(
          (successResp) => {
            if (successResp.data.code == 200) {
              this.$router.replace('./index')
            }
          }
      ).catch(failResp => {
        console.log(failResp)
      })
    }
  }
}
</script>

